.slogan_banner {
  z-index: 1;
  color: #fff;
  font-size: 2.8em;
  position: relative;
  top: 20vh;
  overflow-x: hidden;
  -webkit-overflow-x: hidden !important;
}
.slogan_banner .span_slogan {
  display: block;
}

.buttons {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  z-index: 4;
  padding-top: 24vh;
  gap: 5vw;
}

@media (max-width: 780px) {
  .slogan_banner {
    font-size: 2.5em;
  }
}
@media (max-width: 500px) {
  .slogan_banner {
    font-size: 2.2em;
    top: 24vh;
    padding-bottom: 15vh;
  }
  .buttons {
    padding-top: 18vh;
  }
}
@media (max-width: 500px) {
  .slogan_banner {
    font-size: 1.8em;
    padding-top: 0;
    top: 10vh;
  }
  .buttons {
    gap: 10vw;
    top: -140px;
  }
}